import React from 'react';
import { Typography, Grid, Container, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import EmailIcon from '@material-ui/icons/Email';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: '#a7cceb',
    },
    footer: {
        minHeight: 150,
        marginTop: 40,
        paddingTop: 40,
    },
    wrapIcon: {
        verticalAlign: 'middle',
        display: 'inline-flex'
    }
}));




export default function Footer() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Container maxWidth="md">
                <Grid container direction="row" justifyContent="space-around" className={classes.footer}>
                    <Grid item xs={4}>
                        <Typography>
                            Gabinet kosmetyczny VYSPA
                        </Typography>
                        <Typography>
                            ul. Krakowska 30
                        </Typography>
                        <Typography>
                            33-100 Tarnów
                        </Typography>
                    </Grid>
                    <Grid item xs={4}>
                        <Typography>
                            Rejestracja:
                        </Typography>
                        <Typography>
                            Tel: 883 435 669
                        </Typography>
                        <Typography>
                            <Link href="mailto:vyspa.jm@gmail.com" color="inherit" underline="none" target="_blank" rel="noopener" className={classes.wrapIcon}>
                                <EmailIcon/> vyspa.jm@gmail.com
                            </Link>
                        </Typography>
                        <Typography>
                            <Link href="https://www.instagram.com/vyspa.jola.martyna" color="inherit" underline="none" target="_blank" rel="noopener" className={classes.wrapIcon}>
                                <InstagramIcon /> vyspa.jola.martyna
                            </Link>
                        </Typography>
                        <Typography>
                            <Link href="https://www.facebook.com/vyspa.jolamartyna" color="inherit" underline="none" target="_blank" rel="noopener" className={classes.wrapIcon}>
                                <FacebookIcon /> vyspa.jolamartyna
                            </Link>
                        </Typography>

                    </Grid>
                    <Grid item xs={4}>
                        <Typography>
                            Godziny otwarcia:
                        </Typography>
                        <Typography>
                            pon-sob: 10:00-20:00
                        </Typography>
                    </Grid>
                </Grid>
            </Container>
        </div>
    )
}
