import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import AppNav from './AppNav';
import Home from './pages/Home';
import Cennik from './pages/Cennik';
import About from './pages/About';
import Gabinet from './pages/Gabinet';
import Kobido from './pages/services/Kobido';
import Kassaji from './pages/services/Kassaji';
import Kyukaku from './pages/services/Kyukaku';

import Kontakt from './pages/Kontakt';
import Footer from './Footer';
import { createTheme, ThemeProvider } from '@material-ui/core/styles';
import './App.css';

const poppinsFont = '"Poppins", sans-serif;'
const firaFont = '"Fira Sans", sans-serif;'
const loraFont = '"Lora", serif;'

const theme = createTheme({
  palette: {
    primary: {
      // light: will be calculated from palette.primary.main,
      main: '#a7cceb',
      dark: '#003159',
      // contrastText: will be calculated to contrast with palette.primary.main
    },
  },
  typography: {
    fontFamily: poppinsFont,
  }
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <AppNav />
        <Switch>
          <Route path="/cennik">
            <Cennik />
          </Route>
          <Route path="/o-mnie">
            <About />
          </Route>
          <Route path="/kobido">
            <Kobido />
          </Route>
          <Route path="/kassaji">
            <Kassaji />
          </Route>
          <Route path="/kyukaku">
            <Kyukaku />
          </Route>
          <Route path="/cennik">
            <Cennik />
          </Route>
          <Route path="/gabinet">
            <Gabinet />
          </Route>
          <Route path="/kontakt">
            <Kontakt />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
        <Footer />
      </Router>
    </ThemeProvider>
  );
}

export default App;
