import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Grid, Container, Link } from '@material-ui/core';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import EmailIcon from '@material-ui/icons/Email';
import map from '../images/map.png';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    header: {
        marginTop: 100,
        minHeight: 150,
        backgroundColor: '#a7cceb',
        textAlign: 'center',
        paddingTop: 40,
    },
    main: {
        paddingTop: 100,
        paddingBottom: 100,
    },
    map: {
        height: 300,
    },
    wrapIcon: {
        verticalAlign: 'middle',
        display: 'inline-flex'
    }
}));




export default function Kontakt() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Grid container direction="column">
                <Grid item justifyContent="center" alignItems="center" className={classes.header}>
                    <Typography variant="h2">
                        Kontakt
                    </Typography>
                </Grid>
            <Grid item className={classes.main}>
                    <Container maxWidth="md">
                        <Grid container alignItems="center">
                            <Grid item xs={6}>
                                <Typography variant="h3">
                                    <img alt="Jola" src={map} className={classes.map} />
                                </Typography>
                            </Grid>
                            <Grid item xs={6} container spacing={2}>
                                <Grid item xs={12}>
                                    <Typography variant="h4">
                                        Gabinet kosmetyczny VYSPA
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography>
                                        ul. Krakowska 30
                                    </Typography>
                                    <Typography>
                                        33-100 Tarnów
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography variant="h5">
                                        Godziny otwarcia:
                                    </Typography>
                                    <Typography>
                                        poniedziałek-sobota: 10:00-20:00
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography variant="h5">
                                        Rejestracja:
                                    </Typography>
                                    <Typography>
                                        Tel: 883 435 669
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography>
                                        <Link href="mailto:vyspa.jm@gmail.com" color="inherit" underline="none" target="_blank" rel="noopener" className={classes.wrapIcon}>
                                            <EmailIcon/> vyspa.jm@gmail.com
                                        </Link>
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography>
                                        <Link href="https://www.instagram.com/vyspa.jola.martyna" color="inherit" underline="none" target="_blank" rel="noopener" className={classes.wrapIcon}>
                                            <InstagramIcon /> vyspa.jola.martyna
                                        </Link>
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography>
                                        <Link href="https://www.facebook.com/vyspa.jolamartyna" color="inherit" underline="none" target="_blank" rel="noopener" className={classes.wrapIcon}>
                                            <FacebookIcon /> vyspa.jolamartyna
                                        </Link>
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Container>
                </Grid>
            </Grid>
        </div>
    )
}
